import { createSlice } from '@reduxjs/toolkit';

const authSlice = createSlice({
  name: 'auth',
  initialState: {
    isAuthenticated: false,
    user: null, // Moesz przechowywa dane uytkownika, np. token, tutaj
  },
  reducers: {
    login: (state, action) => {
      // Po zalogowaniu, ustaw stan autoryzacji na true
      state.isAuthenticated = true;
      // Zapisz dane uytkownika, np. token, jeli takie dostajesz z serwera
      state.user = action.payload; // Przykad uycia: { token: 'xyz123' }
    },
    logout: (state) => {
      // Po wylogowaniu, zresetuj stan autoryzacji i usu dane uytkownika
      state.isAuthenticated = false;
      state.user = null;
    },
    register: (state, action) => {
      // Po zarejestrowaniu, ustaw stan autoryzacji na true
      state.isAuthenticated = true;
      // Zapisz dane uytkownika, np. token, jeli takie dostajesz z serwera
      state.user = action.payload; // Przykad uycia: { token: 'xyz123' }
    },
  },
});

export const { login, logout, register } = authSlice.actions;

export default authSlice.reducer;
